<template>
	<view>
		<view class="selector-wrapper">
			<!-- title -->
			<view class="selector-title line_under">
				{{ currentItem.id }}.{{ currentItem.title }}
			</view>
			<!-- describe -->
			<view class="selector-describe">
				{{ currentItem.describe }}
			</view>
			<!-- question -->
			<view class="selector-question line_under">
				{{ currentItem.question }}
			</view>
			<!-- option -->
			<view class="selector-option" :style="{'background': item.nextId === checkedId ? 'rgb(84, 84, 168)' : '#8080ff'}" v-for="item in currentItem.options" :key="item.nextId" @click="radioChange(item)">
				<radio :checked="item.nextId === checkedId" />
				<text>{{ item.nextId }}</text>.<text>{{ item.optionText }}</text>
			</view>
		</view>
		<!-- button -->
		<!-- <view class="selector-button">
			<button style="float:left;" :disabled="currentId === 1" @tap="previous()">上一步</button>
			<button style="float:right;" v-show="currentItem.options && currentItem.options.length > 0" :disabled="!checkedId" @tap="next(checkedId)">下一步</button>
			<button style="float:right;" v-show="!(currentItem.options && currentItem.options.length > 0)" @tap="restart()">重新开始</button>
		</view> -->
	</view>
</template>

<script>
	export default {
		props: {
			dataList: {
				type: Array,
				default:function(){
					return []
				}
			}
		},
		data() {
			return {
				selectedList: [1],
				checkedId: null,
				currentId: 1,
				currentItem: {}
			}
		},
		watch: {
			dataList(val, oldVal) {
				this.dataList.map(v => {
					if (v.id === this.currentId) {
						this.currentItem = v
						return
					}
				})
			}
		},
		methods: {
			// 上一步
			// previous() {
			// 	this.checkedId = this.selectedList.pop()
			// 	const targetId = this.selectedList[this.selectedList.length - 1]
			// 	this.switchQuestion(targetId)
			// },
			// // 下一步
			// next(id) {
			// 	this.checkedId = null
			// 	const targetId = id
			// 	this.switchQuestion(targetId)
			// 	this.selectedList.push(id)
			// },
			// // 重新开始
			// restart() {
			// 	this.selectedList = [1]
			// 	this.switchQuestion(1)
			// 	this.checkedId = null
			// },
			// 变更
			radioChange(item) {
				this.checkedId = item.nextId
			},
			// 切换
			switchQuestion(targetId) {
				this.dataList.map(v => {
					if (v.id === targetId) {
						this.currentId = targetId
						this.currentItem = v
						return
					}
				})
			}
		}
	}
</script>

<style scoped lang="scss">
	.selector-wrapper {
		margin: 20rpx 40rpx;
		border-radius: 10rpx;
		background-color: #fff;
	}
	.selector-inner {
		padding: 0 20rpx;
	}
	.selector-title {
		font-size: 35rpx;
		padding: 10rpx 0;
	}
	.selector-option {
		font-size: 30rpx;
		padding: 20rpx;
		margin:30rpx 0;
		border-radius: 10rpx;
		color: white;
	}
	.selector-button {
		width: 100%;
		position: fixed;
		bottom: 0;
		bottom: constant(safe-area-inset-bottom);
		bottom: env(safe-area-inset-bottom);
		button{
			width:50%;
			display: inline-block;
			border: 2rpx solid #9595ff;
			border-radius: 0;
			background: #8080ff;
			color: white;
		}
		button[disabled]{
			background: #ababff;
			color: white;
		}
	}
	/* 以下为实现0.5px底部边界 */
	.line_under {
		position: relative;
	}
	.line_under:before,
	.line_under:after {
		position: absolute;
		content: " ";
		height: 1px;
		width: 100%;
		left: 0;
		transform-origin: 0 0;
		-webkit-transform-origin: 0 0;
	}
	.line_under:after {
		bottom: 0;
		border-bottom: 1px solid gainsboro;
	}
	@media only screen and (-webkit-min-device-pixel-ratio: 1.5) {
		.line_under:after,
		.line_under:before {
			-webkit-transform: scaleY(0.667);
		}
	}
	@media only screen and (-webkit-min-device-pixel-ratio: 2) {
		.line_under:after,
		.line_under:before {
			-webkit-transform: scaleY(0.5);
		}
	}
</style>
